<template>
	<div class="main_header">
		<div class="main_header_left">
			<!-- <img src="../../assets/images/logo.svg"/> -->
			<h3></h3>
		</div>
		<div class="main_header_right">
			<div class="main_header_temp_hum">
				<div class="main_header_temp">
					<span> </span>
					<span class="temp_numb">{{ temp }}</span>
				</div>
				<div class="main_header_hum">
					<span> </span>
					<span class="hum_numb">{{ humidity }}%</span>
				</div>
			</div>
			<div class="main_header_date">
				<div class="new_date"> {{ nowTime }} </div>
				<div class="new_week">
					<div>{{ getWeek }}</div>
					<div>{{ nowDate }}</div>
				</div>
			</div>
			<div class="rightta">
				<span class="main_header_exit houtai"><a href="/default"></a></span>
			</div>
			<div class="righttc">
				<span class="main_header_exit" @click="handleExit">
					<!-- <a href="/"></a>  -->
					<a :href="href"></a>
					<!--  -->
				</span>
			</div>
		</div>
	</div>
</template>

<script>
import header from "./header.js";
export default header;
</script>

<style >
@import url("header.css");
@import url("../../assets/css/common.css");
</style>


